import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addEducation } from "../../actions/profile";

const AddEducation = ({ addEducation, history }) => {
    const [expFormData, setexpFormData] = useState({
        school: "",
        degree: "",
        fieldofstudy: "",
        from: "",
        to: "",
        current: false,
        description: "",
    });

    const [toDateDisabled, setToDateDisabled] = useState(false);

    const {
        school,
        degree,
        fieldofstudy,
        from,
        to,
        current,
        description
    } = expFormData;

    const onChange = event => setexpFormData({ ...expFormData, [event.target.name]: event.target.value, })

    const onSubmitHandler = (e) => {
        e.preventDefault();
        addEducation(expFormData, history)
    }
    return (
        <>
            <h1 className="large text-primary">
                Add Your Education
      </h1>
            <p className="lead">
                <i className="fas fa-code-branch"></i> Add any school or bootcamp that you have attended.</p>
            <small>* = required field</small>
            <form className="form" onSubmit={onSubmitHandler}>
                <div className="form-group">
                    <input type="text" placeholder="* School or Bootcamp Title" name="school" value={school} required onChange={onChange} />
                </div>
                <div className="form-group">
                    <input type="text" placeholder="* Degree or Certificate" name="degree" value={degree} required onChange={onChange} />
                </div>
                <div className="form-group">
                    <input type="text" placeholder="Field of study" name="fieldofstudy" value={fieldofstudy} onChange={onChange} />
                </div>
                <div className="form-group">
                    <h4>From Date</h4>
                    <input type="date" name="from" value={from} onChange={onChange} />
                </div>
                <div className="form-group">
                    <p><input type="checkbox" name="current" checked={current} onChange={e => { setexpFormData({ ...expFormData, current: !current }); setToDateDisabled(!toDateDisabled) }} /> Current Job</p>
                </div>
                <div className="form-group">
                    <h4>To Date</h4>
                    <input type="date" name="to" value={to} onChange={onChange} disabled={toDateDisabled} />
                </div>
                <div className="form-group">
                    <textarea
                        name="description" value={description}
                        cols="30"
                        rows="5"
                        placeholder="Program Description"
                        onChange={onChange}
                    ></textarea>
                </div>
                <input type="submit" className="btn btn-primary my-1" />
                <a className="btn btn-light my-1" href="dashboard.html">Go Back</a>
            </form>
        </>
    )
}

AddEducation.propTypes = {
    addEducation: PropTypes.func.isRequired,
}


export default connect(null, { addEducation })(AddEducation)
