import { ADD_POST, COMMENT_ADDED, COMMENT_REMOVED, DELETE_POST, GET_POST, GET_POSTS, POST_ERROR, UPDATE_LIKES } from "../actions/types";
const initialState = {
    posts: [],
    post: null,
    loading: true,
    error: {}
}

export default function postReducer (state = initialState, action) {
    const { type, payload } = action;


    switch (type) {
        case GET_POSTS:
            return {
                ...state,
                posts: payload,
                loading: false,
            }
        case GET_POST:

            return {
                ...state,
                post: payload,
                loading: false,
            }
        case POST_ERROR:
            return {
                ...state,
                error: payload,
                loading: false,
            }
        case ADD_POST:
            return {
                ...state,
                posts: [...state.posts, payload],
                loading: false,
            }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(post => post._id !== payload.id),
            }
        case UPDATE_LIKES:
            return {
                ...state,
                posts: state.posts.map(post => post._id === payload.id ? { ...post, likes: payload.likes } : post),
                loading: false,
            }
        case COMMENT_ADDED:
            return {
                ...state,
                post: { ...state.post, comments: payload },
                loading: false,
            }
        case COMMENT_REMOVED:
            return {
                ...state,
                post: {
                    ...state.post,
                    comments: state.post.comments.filter(comment => comment._id !== payload),
                    loading: false,
                }
            }
        default:
            return state;
    }
}