import { CLEAR_PROFILE, GET_PROFILE, GET_PROFILES, GET_REPOS, PROFILE_ERROR, UPDATE_PROFILE } from "../actions/types";

const initialState = {
    profile: null,
    profiles: [],
    repos: [],
    loading: true,
    error: {}
}

export default function profileReducer(state = initialState, action) {
    const { type, payload } = action;
    switch (type) {
        case UPDATE_PROFILE:
        case GET_PROFILE:
            return {
                ...state,
                profile: payload,
                loading: false,
            }
        case GET_REPOS:
            return {
                ...state,
                loading: false,
                repos: payload
            }
        case GET_PROFILES:
            return {
                ...state,
                loading: false,
                profiles: payload,
            }
        case PROFILE_ERROR:
            return {
                ...state,
                error: payload,
                loading: false,
            }
        case CLEAR_PROFILE:
            return {
                ...state,
                error: payload,
                profile: null,
                repos: [],
                loading: false,
            }
        default:
            return state;
    }
}