import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addExperience } from "../../actions/profile";

const AddExperience = ({ addExperience, history }) => {
    const [expFormData, setexpFormData] = useState({
        company: "",
        title: "",
        location: "",
        from: "",
        to: "",
        current: false,
        description: "",
    });

    const [toDateDisabled, setToDateDisabled] = useState(false);

    const {
        company,
        title,
        location,
        from,
        to,
        current,
        description
    } = expFormData;

    const onChange = event => setexpFormData({ ...expFormData, [event.target.name]: event.target.value, })

    const onSubmitHandler = (e) => {
        e.preventDefault();
        addExperience(expFormData,history)
    }
    return (
        <>
            <h1 className="large text-primary">
                Add An Experience
      </h1>
            <p className="lead">
                <i className="fas fa-code-branch"></i> Add any developer/programming
        positions that you have had in the past
      </p>
            <small>* = required field</small>
            <form className="form" onSubmit={onSubmitHandler}>
                <div className="form-group">
                    <input type="text" placeholder="* Job Title" name="title" value={title} required onChange={onChange} />
                </div>
                <div className="form-group">
                    <input type="text" placeholder="* Company" name="company" value={company} required onChange={onChange} />
                </div>
                <div className="form-group">
                    <input type="text" placeholder="Location" name="location" value={location} onChange={onChange} />
                </div>
                <div className="form-group">
                    <h4>From Date</h4>
                    <input type="date" name="from" value={from} onChange={onChange} />
                </div>
                <div className="form-group">
                    <p><input type="checkbox" name="current" checked={current} onChange={e => { setexpFormData({ ...expFormData, current: !current }); setToDateDisabled(!toDateDisabled) }} /> Current Job</p>
                </div>
                <div className="form-group">
                    <h4>To Date</h4>
                    <input type="date" name="to" value={to} onChange={onChange} disabled={toDateDisabled} />
                </div>
                <div className="form-group">
                    <textarea
                        name="description" value={description}
                        cols="30"
                        rows="5"
                        placeholder="Job Description"
                        onChange={onChange}
                    ></textarea>
                </div>
                <input type="submit" className="btn btn-primary my-1" />
                <a className="btn btn-light my-1" href="dashboard.html">Go Back</a>
            </form>
        </>
    )
}

AddExperience.propTypes = {
    addExperience: PropTypes.func.isRequired,
}


export default connect(null, { addExperience })(AddExperience)
