import React, { Fragment, useState } from 'react'
import { Link, Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { setAlert } from "../../actions/alert";
import { register } from "../../actions/auth";
import PropTypes from 'prop-types';

const Register = ({ setAlert, register, isAuthenticated }) => {
    const [registerFormData, setRegisterFormData] = useState({
        name: "",
        email: "",
        password: "",
        password2: "",
    });

    const {
        name,
        email,
        password,
        password2,
    } = registerFormData;

    const onChangeHandler = e => setRegisterFormData({ ...registerFormData, [e.target.name]: e.target.value });
    const onSubmitHandler = async e => {
        e.preventDefault();
        if (password !== password2) {
            setAlert('Password do not match.', 'danger')
        } else {
            register({ name, email, password });
        }
    }

    if (isAuthenticated) {
        return <Redirect to="/dashboard" />
    }
    return (
        <Fragment>
            <h1 className="large text-primary">Sign Up</h1>
            <p className="lead"><i className="fas fa-user"></i> Create Your Account</p>
            <form className="form" onSubmit={onSubmitHandler}>
                <div className="form-group">
                    <input onChange={onChangeHandler} type="text" placeholder="Name" name="name" value={name} />
                </div>
                <div className="form-group">
                    <input onChange={onChangeHandler} type="email" placeholder="Email Address" name="email" value={email} />
                    <small className="form-text"
                    >This site uses Gravatar so if you want a profile image, use a
            Gravatar email</small
                    >
                </div>
                <div className="form-group">
                    <input
                        onChange={onChangeHandler}
                        type="password"
                        placeholder="Password"
                        name="password"
                        value={password} />
                </div>
                <div className="form-group">
                    <input
                        onChange={onChangeHandler}
                        type="password"
                        placeholder="Confirm Password"
                        name="password2"
                        value={password2} />
                </div>
                <input type="submit" className="btn btn-primary" value="Register" />
            </form>
            <p className="my-1">
                Already have an account? <Link to="/login">Sign In</Link>
            </p>
        </Fragment>
    )
}

Register.propTypes = {
    setAlert: PropTypes.func.isRequired,
    register: PropTypes.func.isRequired,
    isAuthenticated: PropTypes.bool,
}

const mapStateToProps = (state) => {
    return {
        isAuthenticated: state.auth.isAuthenticated,
    }
}

export default connect(mapStateToProps, {
    setAlert,
    register
})(Register);