import React, { Fragment } from 'react'
import PropTypes from 'prop-types'
import Moment from "react-moment"
import { connect } from 'react-redux'
import { deleteExperience } from "../../actions/profile"

const Experiences = ({ experience, deleteExperience }) => {
    const deleteHandler = (event, id) => {
        event.preventDefault();
        deleteExperience(id)
    }
    const experiences = experience.map((exp) => (<tr key={exp._id}>
        <td>{exp.company}</td>
        <td className="hide-sm">{exp.title}</td>
        <td className="hide-sm">
            <Moment format="YYYY/MM/DD">
                {exp.from}
            </Moment> - {
                exp.to === null ? ('Now') : (<Moment format="YYYY/MM/DD">
                    {exp.to}
                </Moment>)
            }
        </td>
        <td className="btn btn-danger" onClick={(event) => deleteHandler(event, exp._id)}>
            {/* <td className="btn btn-danger" onClick={() => alert("Hello")}> */}
            Delete
        </td>
    </tr>))
    return (
        <Fragment>
            <h2 className="my-2">Experience Credentials</h2>
            <table className="table">
                <thead>
                    <tr>
                        <th>Company</th>
                        <th className="hide-sm">Title</th>
                        <th className="hide-sm">Years</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {experiences}
                </tbody>
            </table>
        </Fragment>
    )
}

Experiences.propTypes = {
    experience: PropTypes.array.isRequired,
    deleteExperience: PropTypes.func.isRequired,
}


export default connect(null, { deleteExperience })(Experiences)
